import React from 'react';
import styles from './profile-page.module.css';
import NavigationLink from "../../components/utils/NavigationLink";
import {logout} from "../../services/actions/security/logout";
import {useDispatch} from "../../index";

function ProfilePage({content}: { content: React.ReactElement }) {
    const dispatch = useDispatch();

    return (
        <>
            <div className={styles.page}>
                <nav className={styles.navigation}>
                    <NavigationLink to={"/profile"} name={""}/>
                    <NavigationLink to={"/profile/orders"} name={" "}/>
                    <NavigationLink onClick={() => dispatch(logout())} to={"/login"} name={""}/>
                    <p className={styles.navFooterText}>     
                          </p>
                </nav>
                {content}
            </div>
        </>
    )
}

export default ProfilePage;
