import React, {useCallback} from 'react';
import {Button, Input, PasswordInput} from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "../common-page.module.css";
import {Link, useNavigate} from "react-router-dom";
import {resetPassword} from "../../../services/actions/security/resetPassword";
import {useDispatch} from "../../../index";

function PasswordResetPage() {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [password, setPassword] = React.useState('');
    const [token, setToken] = React.useState('');

    function handlePasswordChange(e: React.ChangeEvent<HTMLInputElement>) {
        setPassword(e.target.value);
    }

    function handleTokenChange(e: React.ChangeEvent<HTMLInputElement>) {
        setToken(e.target.value);
    }

    const handleSubmit = useCallback(async (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        const success = await dispatch(resetPassword(password, token));

        if (success) {
            navigate('/login');
        }
    }, [dispatch, navigate, password, token]);

    return (
        <>
            <section className={styles.container}>
                <form className={styles.body} onSubmit={handleSubmit}>
                    <p className={styles.header}> </p>
                    <PasswordInput
                        onChange={handlePasswordChange}
                        value={password}
                        name={'password'}
                        extraClass="mb-2"
                        placeholder={'  '}
                    />
                    <Input
                        type={'text'}
                        placeholder={'   '}
                        onChange={handleTokenChange}
                        value={token}
                        name={'name'}
                        error={false}
                        errorText={''}
                        size={'default'}
                        extraClass="ml-1"
                    />
                    <Button htmlType="submit" type="primary" size="large">
                        
                    </Button>
                </form>
                <section className={styles.footerContainer}>
                    <p className={styles.bottomText}> ? <Link to="/login"></Link></p>
                </section>
            </section>
        </>
    )
}

export default PasswordResetPage;